<template>
    <Teleport to="body">
<div v-if="isVisible" class="fu-modal__backdrop" @click.self="onClose">
    <div class="fu-modal" :class="`fu-modal--${size}`">
      <!-- Header -->
      <div class="fu-modal__header">
        <h3 class="fu-modal__title">{{ title }}</h3>

        <FusionActionButton
          text=" "
          :icon="X"
          class="fu-modal__close"
          @click="onClose"
          variant="ghost"
          size="sm"
        />
      </div>

      
      <!-- Body -->
      <div class="fu-modal__body">
        <slot>
          Default modal content.
        </slot>
      </div>

      <!-- Footer -->
      <div v-if="showFooter" class="fu-modal__footer">
        <slot name="footer">
         
        </slot>
      </div>
    </div>
  </div>

    </Teleport>
  
</template>

<script setup lang="ts">
import { onMounted, onBeforeUnmount } from "vue";
import FusionActionButton from "../actionButton/FusionActionButton.vue";
import FusionButton from "../button/FusionButton.vue";
import { X } from "lucide-vue-next";

const props = withDefaults(
  defineProps<{
    isVisible: boolean;
    title: string;
    size?: "sm" | "md" | "lg";
    showFooter?: boolean;
  }>(),
  {
    size: "md",
    showFooter: true,
  }
);

const emit = defineEmits<{
  (e: "close"): void;
  (e: "cancel"): void;
  (e: "confirm"): void;
}>();

const onClose = () => emit("close");
const onCancel = () => emit("cancel");
const onConfirm = () => emit("confirm");

function handleKeydown(e: KeyboardEvent) {
  if (e.key === "Escape" || e.key === "Esc") {
    onClose();
  }
}

onMounted(() => {
  window.addEventListener("keydown", handleKeydown);
});

onBeforeUnmount(() => {
  window.removeEventListener("keydown", handleKeydown);
});
</script>

