<template>
  <div class="fu-action-btn-wrapper" :class="{ 'has-tooltip': tooltip }">
    <button
      class="fu-action-btn"
      :class="[`fu-action-btn--${size}`, `fu-action-btn--${variant}`, { 'is-loading': loading }]"
      :disabled="disabled || loading"
      @click="!loading ? $emit('click', $event) : null"
    >
      <span v-if="loading" class="fu-spinner"></span>

      <!-- icon only -->
      <component
        v-else-if="icon"
        :is="icon"
        class="fu-action-btn__icon"
        :size="20"
      />
    </button>

    <span v-if="tooltip" class="fu-tooltip">
      {{ tooltip }}
    </span>
  </div>
</template>

<script setup lang="ts">
import type { Component } from "vue";

interface Props {
  disabled?: boolean;
  size?: "sm" | "md" | "lg";
  variant?: "solid" | "subtle" | "outline" | "ghost" | "danger";
  tooltip?: string | null;
  loading?: boolean;
  icon?: Component | null;
}

withDefaults(defineProps<Props>(), {
  disabled: false,
  size: "md",
  variant: "subtle",
  tooltip: null,
  loading: false,
  icon: null,
});

defineEmits<{
  (e: "click", ev: MouseEvent): void;
}>();



</script>

<style scoped lang="scss">
@import "./FusionActionButton.scss";


</style>
