import type { App, Plugin } from "vue";
import { useToast } from "./composables/useToast";
import "./styles/index.scss";

const modules = import.meta.glob("./components/**/*.vue", { eager: true });

const FusionBinaryUI: Plugin = {
  install(app: App) {
    for (const path in modules) {
      const component = (modules[path] as any).default;
      const name = component.name || path.split("/").pop()?.replace(".vue", "");
      console.log(" âœ… Registering component:", name);
      app.component(name, component);
    }

    // Register globally for Vue components
    app.config.globalProperties.FusionToast = (options: any) => {
      const toast = useToast();
      toast(options);
    };

    // Also make available as a global function in scripts
    if (typeof window !== "undefined") {
      (window as any).FusionToast = (options: any) => {
        const toast = useToast();
        toast(options);
      };
    }
  },
};

export default FusionBinaryUI;

export { UserStatus } from "./components/avatar/UserStatus";
export { useTheme } from "./composables/useTheme";
export { useToast } from "./composables/useToast";

export { default as FuAvatar } from "./components/avatar/FuAvatar.vue";