    background-color: inherit;
    font-size: 14px;
    outline: none;
    cursor: pointer;
    color: var(--fu-color-text);
    transition: border-color 0.15s ease;
    border: 0px;

    &:focus {
      border-color: var(--fu-accent, #2563eb);
    }

  }

  &__menu {
    position: absolute;
    background: var(--fu-surface-background);
    border: .5px solid var(--fu-border);
    border-radius: var(--fu-radius, 6px);
