<template>
  <div class="fu-status-dropdown" ref="dropdown">
    <div v-if="label" class="fu-status-dropdown__label-text">
      {{ label }}
    </div>

    <button class="fu-status-dropdown__button" @click="toggleDropdown">
      <span
        v-if="selected?.color"
        class="fu-status-dropdown__dot"
        :style="{ backgroundColor: selected.color }"
      ></span>
      <span class="fu-status-dropdown__label">
        {{ selected?.label || "Select Status" }}
      </span>
      <ChevronDown class="fu-status-dropdown__chevron" />
    </button>

    <!-- Teleport menu to body -->
    <Teleport to="body">
      <transition name="fade">
        <ul
          v-if="dropdownOpen"
          ref="menuRef"
          class="fu-status-dropdown__menu"
          :style="menuStyle"
        >
          <li
            v-for="option in options"
            :key="option.label"
            class="fu-status-dropdown__item"
            @click="selectStatus(option)"
          >
            <span
              v-if="option.color"
              class="fu-status-dropdown__dot"
              :style="{ backgroundColor: option.color }"
            ></span>
            <span class="fu-status-dropdown__item-label">{{ option.label }}</span>
          </li>
        </ul>
      </transition>
    </Teleport>
  </div>
</template>

<script setup lang="ts">
import { ChevronDown } from "lucide-vue-next"
import { ref, watch, onMounted, onBeforeUnmount, reactive, nextTick } from "vue"

interface Option {
  label: string
  color: string
}

interface Props {
  modelValue: Option | null
  options: Option[]
  align?: "left" | "center" | "right"
  label?: string
}

const props = withDefaults(defineProps<Props>(), {
  align: "left",
  label: "",
})

const emit = defineEmits<{
  (e: "update:modelValue", value: Option): void
}>()

const dropdownOpen = ref(false)
const dropdown = ref<HTMLElement | null>(null)
const menuRef = ref<HTMLElement | null>(null)
const selected = ref<Option | null>(props.modelValue || null)
const menuStyle = reactive<Record<string, string>>({
  visibility: "hidden",
  opacity: "0",
})

watch(
  () => props.modelValue,
  (val) => (selected.value = val)
)

function calculatePosition() {
  if (!dropdown.value) return
  const rect = dropdown.value.getBoundingClientRect()

  menuStyle.position = "absolute"
  menuStyle.zIndex = "9999"
  menuStyle.top = `${rect.bottom + window.scrollY - 2}px`

  if (props.align === "center") {
    menuStyle.left = `${rect.left + window.scrollX + rect.width / 2}px`
    menuStyle.transform = "translateX(-50%)"
  } else if (props.align === "right") {
    menuStyle.left = `${rect.right + window.scrollX}px`
    menuStyle.transform = "translateX(-100%)"
  } else {
    menuStyle.left = `${rect.left + window.scrollX}px`
    menuStyle.transform = "none"
  }

  menuStyle.minWidth = `${rect.width}px`
}

const toggleDropdown = async () => {
  dropdownOpen.value = !dropdownOpen.value
  if (dropdownOpen.value) {
    calculatePosition()
    await nextTick()
    calculatePosition()
    menuStyle.visibility = "visible"
    menuStyle.opacity = "1"
  }
}

const selectStatus = (option: Option) => {
  selected.value = option
  emit("update:modelValue", option)
  dropdownOpen.value = false
}

// âœ… Close when clicking outside, resizing, or scrolling
const handleClickOutside = (event: MouseEvent) => {
  const target = event.target as Node
  if (
    dropdownOpen.value &&
    dropdown.value &&
    menuRef.value &&
    !dropdown.value.contains(target) &&
    !menuRef.value.contains(target)
  ) {
    dropdownOpen.value = false
  }
}

const handleViewportChange = () => {
  if (dropdownOpen.value) dropdownOpen.value = false
}

const handleKeyDown = (e: KeyboardEvent) => {
  if (e.key === "Escape" && dropdownOpen.value) dropdownOpen.value = false
}

onMounted(() => {
  document.addEventListener("click", handleClickOutside)
  window.addEventListener("resize", handleViewportChange)
  window.addEventListener("scroll", handleViewportChange, true)
  document.addEventListener("keydown", handleKeyDown)
})

onBeforeUnmount(() => {
  document.removeEventListener("click", handleClickOutside)
  window.removeEventListener("resize", handleViewportChange)
  window.removeEventListener("scroll", handleViewportChange, true)
  document.removeEventListener("keydown", handleKeyDown)
})
</script>

<style scoped lang="scss">
@import "./FusionStatusDropdown.scss";
</style>
