<template>
  <div class="fu-notification-dropdown" ref="dropdown">
    <!-- Bell Trigger -->
    <div class="fu-notification__trigger" @click="toggleDropdown">
      <div class="fu-bell-icon">
        <component v-if="bellIcon" :is="bellIcon"  :stroke-width="1.5" class="fu-bell-svg" />
        <Bell v-else class="fu-bell-svg" />
        <span v-if="unreadCount > 0" class="fu-badge">{{ unreadCount }}</span>
      </div>
    </div>


    <teleport to="body">
      <transition name="fade">
        <div
          v-if="dropdownOpen"
          ref="panelEl"
          class="fu-notification__panel"
          :style="menuPosition"
        >
          <slot>
            <div class="fu-empty">
              <h4>No Content</h4>
              <p>Use the default slot to pass custom dropdown body.</p>
            </div>
          </slot>
        </div>
      </transition>
    </teleport>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, onBeforeUnmount } from "vue";
import { Bell } from "lucide-vue-next";

interface Props {
  unreadCount?: number;
  bellIcon?: any;
  align?: "left" | "center" | "right";
}

const props = withDefaults(defineProps<Props>(), {
  unreadCount: 0,
  bellIcon: undefined,
  align: "right",
});

const dropdownOpen = ref(false);
const dropdown = ref<HTMLElement | null>(null);
const panelEl = ref<HTMLElement | null>(null);
const menuPosition = ref<Record<string, string>>({});

const toggleDropdown = () => {
  dropdownOpen.value = !dropdownOpen.value;
  if (dropdownOpen.value && dropdown.value) {
    const rect = dropdown.value.getBoundingClientRect();

    let left = rect.left + window.scrollX;
    if (props.align === "right") left = rect.right + window.scrollX - 300;
    else if (props.align === "center") left = rect.left + window.scrollX - 160 + rect.width / 2;

    menuPosition.value = {
      top: `${rect.bottom + window.scrollY + 8}px`,
      left: `${Math.max(left, 8)}px`,
      position: "absolute",
      zIndex: "2000",
    };
  }
};


const handleClickOutside = (event: MouseEvent) => {
  const target = event.target as Node;
  if (
    dropdownOpen.value &&
    dropdown.value &&
    !dropdown.value.contains(target) &&
    (!panelEl.value || !panelEl.value.contains(target))
  ) {
    dropdownOpen.value = false;
  }
};

onMounted(() => document.addEventListener("click", handleClickOutside));
onBeforeUnmount(() => document.removeEventListener("click", handleClickOutside));
</script>

<style scoped lang="scss">
.fu-notification-dropdown {
  position: relative;
  display: inline-block;

  .fu-notification__trigger {
    cursor: pointer;
    position: relative;

    .fu-bell-icon {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;

      .fu-bell-svg {
        width: 22px;
        height: 22px;
        color: #f3f3f3;
      }

      .fu-badge {
        position: absolute;
        top: -6px;
        right: -6px;
        background: #ef4444;
        color: #fff;
        font-size: 11px;
        font-weight: 600;
        border-radius: 50%;
        width: 16px;
        height: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}


.fu-notification__panel {
  position: absolute;
  width: 300px;
  background-color: var(  --fu-surface-background);
  border-radius: 8px;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.15);
  border: 0.5px solid var(--fu-border);
  overflow: hidden;
  animation: dropdown-fade 0.15s ease;
  max-height: 472px;
}


.fu-empty {
  text-align: center;
  padding: 2rem;
  color: #666;

  h4 {
    font-weight: 600;
    margin-bottom: 0.25rem;
  }

  p {
    font-size: 13px;
    color: #777;
  }
}


.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.15s ease;
}
.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

@keyframes dropdown-fade {
  from {
    transform: translateY(-6px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
</style>
