<template>
    <div class="fu-input-wrapper" :style="{ width: formWrapperWidth }">
        <!-- Label -->
        <label v-if="label" class="fu-input-label">
            {{ label }}
            <span v-if="required" class="fu-input-required">*</span>
        </label>

        <div class="fu-code-container" :class="[
            `fu-input--${size}`,
            `fu-input--${variant}`,
            { 'fu-input--error': error },
        ]">
            <input v-for="(digit, index) in codeDigits" :key="index" ref="inputs" type="text" maxlength="1"
                inputmode="numeric" pattern="[0-9]*" class="fu-code-box fu-input-container" v-model="codeDigits[index]"
                @input="onInput(index, $event)" @keydown.backspace="onBackspace(index)" :disabled="disabled" />
        </div>

        <!-- Error message -->
        <span v-if="error" class="fu-input-error">{{ error }}</span>
    </div>
</template>

<script setup lang="ts">
import { ref, watch, nextTick } from "vue";

interface Props {
    modelValue?: string;
    label?: string;
    length?: number;
    size?: "sm" | "md" | "lg";
    variant?: "outline" | "subtle" | "plain";
    disabled?: boolean;
    error?: string | null;
    required?: boolean;
    formWrapperWidth?: string;
}

const props = withDefaults(defineProps<Props>(), {
    modelValue: "",
    label: "",
    length: 6,
    size: "md",
    variant: "outline",
    disabled: false,
    error: null,
    required: false,
    formWrapperWidth: "fit-content",
});

const emit = defineEmits<{
    (e: "update:modelValue", value: string): void;
}>();

const codeDigits = ref<string[]>(Array(props.length).fill(""));
const inputs = ref<HTMLInputElement[]>([]);

watch(
    () => props.modelValue,
    (val) => {
        if (val && val.length === props.length) {
            codeDigits.value = val.split("");
        }
    }
);

watch(codeDigits, () => {
    emit("update:modelValue", codeDigits.value.join(""));
});

function onInput(index: number, e: Event) {
    const value = (e.target as HTMLInputElement).value.replace(/\D/g, "");
    codeDigits.value[index] = value;

    if (value && index < props.length - 1) {
        nextTick(() => inputs.value[index + 1]?.focus());
    }
}

function onBackspace(index: number) {
    if (!codeDigits.value[index] && index > 0) {
        nextTick(() => inputs.value[index - 1]?.focus());
    }
}
</script>

