import { ref, watchEffect } from "vue";

export type ThemeMode = "light" | "dark" | "auto";

// Read user preference or fallback to auto
const saved = (localStorage.getItem("theme") as ThemeMode) || "auto";
const theme = ref<ThemeMode>(saved);

function getSystemTheme(): "light" | "dark" {
  return window.matchMedia("(prefers-color-scheme: dark)").matches
    ? "dark"
    : "light";
}

function applyTheme(value: ThemeMode, init = false) {

  const resolved = value === "auto" ? getSystemTheme() : value;

  
  if (init) {
    document.documentElement.setAttribute("data-theme", resolved);
    return;
  }


  document.documentElement.setAttribute("data-theme", resolved);
  localStorage.setItem("theme", value);
}


applyTheme(saved, true);

// ðŸŽ§ Listen for system theme changes (only if in auto)
const media = window.matchMedia("(prefers-color-scheme: dark)");
media.addEventListener("change", () => {
  if (theme.value === "auto") applyTheme("auto");
});

// ðŸ‘€ Reactively apply whenever user toggles
watchEffect(() => {
  applyTheme(theme.value);
});

export function useTheme() {
  return {
    theme,
    setTheme: (mode: ThemeMode) => {
      theme.value = mode;
      applyTheme(mode);
    },
    toggleTheme: () => {
      theme.value = theme.value === "dark" ? "light" : "dark";
      applyTheme(theme.value);
    },
  };
}
