<template>
  <div class="fu-kanban scrollbar__control customScrollBar">
    <div class="fu-kanban__columns" @dragover.prevent>
      <!-- Each column -->
      <div
        v-for="(col, colIndex) in localColumns"
        :key="col.id"
        class="fu-kanban__column"
        :class="{ 'fu-kanban__column--drag': editMode }"
        :draggable="editMode"
        @dragstart="onColumnDragStart(colIndex)"
        @drop="onColumnDrop(colIndex)"
        @dragover.prevent
      >
        <!-- Column Header -->
        <header class="fu-kanban__column-header">
          <div class="flex flex--center flex--space">
            <div class="fu-kanban__column-title">
              <span class="fu-kanban__dot" :style="{ background: col.color || '#9ca3af' }"></span>
              <span class="fu-kanban__column-name" :title="col.title">{{ col.title }}</span>
            </div>

            <div class="fu-kanban__header-right">
              <button
                class="fu-kanban__add-item-btn"
                :title="addItemButtonText"
                @click.stop="onAddItem(col, colIndex)"
              >
                +
              </button>
            </div>
          </div>

          <div class="flex flex--center flex--gap-sm">
            <slot name="column-header"></slot>
            <span class="fu-kanban__count">{{ col.items.length }}</span>
          </div>
        </header>

        <!-- ======================
             EDIT MODE
        ======================= -->
        <template v-if="editMode">
          <div class="fu-kanban__edit-body">
            <slot name="edit-column" :column="col" :index="colIndex" />
          </div>
        </template>

        <!-- ======================
             VIEW MODE
        ======================= -->
        <template v-else>
          <!-- Cards -->
          <div
            class="fu-kanban__cards scrollbar__control customScrollBar"
            @dragover.prevent="onItemDragOver(col.id)"
            @dragleave="onItemDragLeave"
            @drop="onItemDrop(col.id, null)"
            :class="{ 'fu-kanban__cards--hover': hoveredColumnId === col.id }"
          >
            <div
              v-for="(item, index) in col.items"
              :key="item.id"
              class="fu-kanban__card"
              draggable="true"
              @dragstart="onItemDragStart(col.id, item)"
              @drop="onItemDrop(col.id, index)"
              @click="onCardClick(item, col)"
            >
              <header class="fu-kanban__card-header">
                <strong>{{ item.title }}</strong>
              </header>

              <div class="fu-kanban__card-body">
                <slot name="card-body" :item="item" :column="col" />
              </div>
            </div>

            <!-- Empty placeholder -->
            <div v-if="!col.items.length" class="fu-kanban__empty">
              {{ noItemtext }}
            </div>
          </div>

          <!-- Add item button (bottom of column) -->
          <button class="fu-kanban__add-card" @click="onAddItem(col, colIndex)">
            {{ addItemButtonText }}
          </button>
        </template>
      </div>

      <!-- Add New Column -->
      <div v-if="editMode" class="fu-kanban__add-column" @click="addColumn">
        {{ addColumnButtonText }}
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, watch } from "vue"

export interface KanbanItem {
  id: string
  title: string
  position: number
  [key: string]: any
}

export interface KanbanColumn {
  id: string
  title: string
  color?: string
  probability?: number
  position: number
  items: KanbanItem[]
}

const props = defineProps<{
  columns: KanbanColumn[]
  editMode?: boolean
  addItemButtonText?: string
  addColumnButtonText?: string
  noItemtext?: string
}>()

const emit = defineEmits<{
  "update:columns": [columns: KanbanColumn[]]
  "update:items": [columns: KanbanColumn[]]
  "card-click": [payload: { id: string; item: KanbanItem; column: KanbanColumn }]
  "add-item": [payload: { column: KanbanColumn; index: number }]
}>()

const addItemButtonText = props.addItemButtonText || "+ Add Item"
const addColumnButtonText = props.addColumnButtonText || "+ Add Stage"

const localColumns = ref<KanbanColumn[]>(JSON.parse(JSON.stringify(props.columns || [])))
const dragContext = ref<{ fromColumnId: string; item: KanbanItem } | null>(null)
const columnDragContext = ref<number | null>(null)
const hoveredColumnId = ref<string | null>(null)

watch(
  () => props.columns,
  (newVal) => {
    localColumns.value = JSON.parse(JSON.stringify(newVal))
  },
  { deep: true }
)

/* =======================================================
   ITEM DRAG LOGIC
======================================================= */
function onItemDragStart(fromColumnId: string, item: KanbanItem) {
  dragContext.value = { fromColumnId, item }
}

function onItemDragOver(columnId: string) {
  hoveredColumnId.value = columnId
}

function onItemDragLeave() {
  hoveredColumnId.value = null
}

function onItemDrop(toColumnId: string, targetIndex: number | null) {
  const ctx = dragContext.value
  if (!ctx) return

  const fromCol = localColumns.value.find((c) => c.id === ctx.fromColumnId)
  const toCol = localColumns.value.find((c) => c.id === toColumnId)
  if (!fromCol || !toCol) return

  fromCol.items = fromCol.items.filter((i) => i.id !== ctx.item.id)
  if (targetIndex === null) toCol.items.push(ctx.item)
  else toCol.items.splice(targetIndex, 0, ctx.item)

  emit("update:items", localColumns.value)
  dragContext.value = null
  hoveredColumnId.value = null
}

/* =======================================================
   COLUMN DRAG LOGIC (for EDIT MODE)
======================================================= */
function onColumnDragStart(fromIndex: number) {
  if (!props.editMode) return
  columnDragContext.value = fromIndex
}

function onColumnDrop(toIndex: number) {
  if (!props.editMode) return
  const fromIndex = columnDragContext.value
  if (fromIndex === null || fromIndex === toIndex) return

  const cols = [...localColumns.value]
  const [moved] = cols.splice(fromIndex, 1)
  cols.splice(toIndex, 0, moved)
  localColumns.value = cols.map((c, i) => ({ ...c, position: i + 1 }))

  emit("update:columns", localColumns.value)
  columnDragContext.value = null
}

/* =======================================================
   COLUMN + ITEM ACTIONS
======================================================= */
function addColumn() {
  const newColumn: KanbanColumn = {
    id: `col-${localColumns.value.length + 1}`,
    title: `Stage ${localColumns.value.length + 1}`,
    color: "#8B5CF6",
    position: localColumns.value.length + 1,
    items: [],
  }
  localColumns.value.push(newColumn)
  emit("update:columns", localColumns.value)
}

function onAddItem(column: KanbanColumn, index: number) {
  emit("add-item", { column, index })
}

function onCardClick(item: KanbanItem, column: KanbanColumn) {
  emit("card-click", { id: item.id, item, column })
}
</script>

