<template>
  <div
    class="fu-upload"
    :class="{ dragging: isDragging }"
    @click="triggerFileSelect"
    @dragover.prevent="isDragging = true"
    @dragleave.prevent="isDragging = false"
    @drop.prevent="handleDrop"
  >
    <div class="fu-upload__content">
      <!-- File Previews -->
      <div v-if="previews.length" class="fu-upload__previews">
        <div
          v-for="(src, index) in previews"
          :key="index"
          class="fu-upload__preview-item"
        >
          <img
            v-if="isImage(src)"
            :src="src"
            class="fu-upload__preview-img"
            alt="Preview"
          />
          <div v-else class="fu-upload__file-fallback">
            <MonitorSmartphone :size="20" />
            <span>File {{ index + 1 }}</span>
          </div>
        </div>
      </div>

      <!-- Upload Prompt -->
      <div v-else class="fu-upload__prompt">
        <MonitorSmartphone class="fu-upload__icon" :size="22" />
        <slot name="description">
          <p class="fu-upload__text">Drag & drop files or click to browse</p>
        </slot>
      </div>

      <!-- Hidden File Input -->
      <input
        ref="fileInput"
        type="file"
        class="fu-upload__input"
        :multiple="multiple"
        :accept="accept"    
        @change="handleFileChange"
      />
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref } from "vue";
import { MonitorSmartphone } from "lucide-vue-next";

const props = defineProps({
  multiple: { type: Boolean, default: false },
  accept: { type: String, default: "*" }, 
});

const isDragging = ref(false);
const previews = ref<string[]>([]);
const fileInput = ref<HTMLInputElement | null>(null);

const emit = defineEmits<{
  (e: "filesSelected", files: FileList): void;
}>();

function triggerFileSelect() {
  fileInput.value?.click();
}

function handleFileChange(event: Event) {
  const target = event.target as HTMLInputElement;
  if (!target.files?.length) return;
  emit("filesSelected", target.files);
  generatePreviews(target.files);
}

function handleDrop(event: DragEvent) {
  isDragging.value = false;
  const files = event.dataTransfer?.files;
  if (!files?.length) return;
  emit("filesSelected", files);
  generatePreviews(files);
}

function generatePreviews(files: FileList) {
  previews.value = [];
  Array.from(files).forEach((file) => {
    if (file.type.startsWith("image/")) {
      const reader = new FileReader();
      reader.onload = (e) => {
        if (typeof e.target?.result === "string") {
          previews.value.push(e.target.result);
        }
      };
      reader.readAsDataURL(file);
    } else {
      previews.value.push("non-image");
    }
  });
}

function isImage(src: string) {
  return src.startsWith("data:image") || src.startsWith("http");
}
</script>

