<template>
  <div class="fu-progress-stepper">
  
    <div class="fu-progress-bar">
      <div class="fu-progress-fill" :style="{ width: progressWidth }"></div>
    </div>

  
    <div v-if="showLabels && steps" class="fu-step-labels">
      <div
        v-for="(step, i) in steps"
        :key="i"
        class="fu-step-label"
        :class="{ active: currentStep === i }"
        @click="onStepClick(i)"
      >
        <div class="circle">{{ i + 1 }}</div>
        <div class="label">{{ step.title }}</div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { computed } from "vue"

const props = defineProps<{
  currentStep: number
  totalSteps: number
  showLabels?: boolean
  steps?: { title: string }[]
}>()

const emit = defineEmits(["step-click"])

const progressWidth = computed(() =>
  `${((props.currentStep + 1) / props.totalSteps) * 100}%`
)

const onStepClick = (index: number) => emit("step-click", index)
</script>

<style scoped lang="scss">

.fu-progress-bar {
  height: 5px;
  background: #e5e7eb;
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 0.75rem;

  .fu-progress-fill {
    height: 100%;
    background: #007aff;
    transition: width 0.3s ease-in-out;
  }
}

/* --- Step Circles and Labels --- */
.fu-step-labels {
  display: flex;
  justify-content: space-between;
  gap: 0.5rem;
  text-align: center;

  .fu-step-label {
    flex: 1;
    cursor: pointer;
    transition: all 0.2s ease;

    .circle {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      border: 2px solid #ccc;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 auto 0.25rem auto;
      font-size: 0.9rem;
      font-weight: 500;
      background: white;
      color: #555;
      transition: all 0.25s ease;
    }

    .label {
      font-size: 0.75rem;
      color: #666;
      transition: color 0.25s ease;
    }

    &.active .circle {
      background: #007aff;
      color: #fff;
      border-color: #007aff;
    }

    &.active .label {
      color: #007aff;
      font-weight: 600;
    }

    &:hover .circle {
      border-color: #007aff;
    }

    &:hover .label {
      color: #007aff;
    }
  }
}
</style>
