<template>
  <div class="fu-btn-wrapper" :class="{ 'has-tooltip': tooltip }">

    <component :is="link ? 'a' : 'button'" class="fu-btn"
      :class="[`fu-btn--${variant}`, `fu-btn--${size}`, { 'is-loading': loading }]" :href="link || undefined"
      :target="link ? target : undefined" :rel="link && target === '_blank' ? 'noopener noreferrer' : undefined"
      :disabled="!link && (disabled || loading)" @click="!link && !loading ? $emit('click', $event) : null">


      <span v-if="loading" class="fu-spinner"></span>

      <component v-if="icon" :is="icon" class="fu-btn-icon" :size="16" />

      <span v-if="loading">
        {{ loadingText || "Loading..." }}
      </span>
      <span v-else>
         <slot>{{ text }}</slot>
      </span>
    </component>


    <span v-if="tooltip" class="fu-tooltip">
      {{ tooltip }}
    </span>
  </div>
</template>

<script setup lang="ts">
import type { Component } from "vue";
interface Props {
  text?: string;
  disabled?: boolean;
  size?: "sm" | "md" | "lg";
  variant?: "solid" | "subtle" | "outline" | "ghost" | "danger";
  tooltip?: string | null;
  loading?: boolean;
  loadingText?: string | null;
  link?: string | null;
  icon?: Component | null;
  target?: "_parent" | "_blank";
}

withDefaults(defineProps<Props>(), {
  text: "Click Me",
  disabled: false,
  size: "sm",
  variant: "solid",
  tooltip: null,
  loading: false,
  loadingText: null,
  link: null,
  icon: null,
  target: "_parent",
});

defineEmits<{
  (e: "click", ev: MouseEvent): void;
}>();
</script>
<style lang="scss" scoped>
@import "./FusionButton.scss";
</style>
