<template>
  <div class="fu-color-popover-wrapper" ref="triggerRef">
    <!-- ðŸŽ¯ Trigger slot -->
    <slot name="trigger" :open="togglePopover" />

    <!-- ðŸŽ¨ Popover -->
    <teleport to="body">
      <transition name="fade">
        <div
          v-if="isOpen"
          class="fu-color-popover"
          :style="popoverPosition"
          ref="popoverRef"
          @click.stop
        >
          <FusionColour
            v-model="color"
            @change="handleChange"
          />
        </div>
      </transition>
    </teleport>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, watch, onMounted, onBeforeUnmount, nextTick } from "vue";
import FusionColour from "@/components/colourPallet/FusionColour.vue";

const props = defineProps<{
  modelValue?: string;
}>();

const emit = defineEmits<{
  (e: "update:modelValue", value: string): void;
  (e: "change", value: string): void;
}>();

const isOpen = ref(false);
const color = ref(props.modelValue || "#000000");
const triggerRef = ref<HTMLElement | null>(null);
const popoverRef = ref<HTMLElement | null>(null);
const position = ref({ top: 0, left: 0 });

/* ðŸ§  Compute popover position */
const popoverPosition = computed(() => ({
  top: position.value.top + "px",
  left: position.value.left + "px",
}));

/* ðŸ§® Toggle + position logic */
function togglePopover() {
  if (isOpen.value) {
    isOpen.value = false;
    return;
  }
  nextTick(() => {
    if (triggerRef.value) {
      const rect = triggerRef.value.getBoundingClientRect();
      position.value = {
        top: rect.bottom + window.scrollY + 6,
        left: rect.left + window.scrollX,
      };
      isOpen.value = true;
    }
  });
}

/* ðŸ”„ Prop sync */
watch(
  () => props.modelValue,
  (val) => {
    if (val && val !== color.value) color.value = val;
  }
);

/*  Emit updates */
function handleChange(newColor: string) {
  emit("update:modelValue", newColor);
  emit("change", newColor);
}

/* ðŸ§¹ Close on outside click */
function handleOutsideClick(e: MouseEvent) {
  if (
    isOpen.value &&
    popoverRef.value &&
    !popoverRef.value.contains(e.target as Node) &&
    !triggerRef.value?.contains(e.target as Node)
  ) {
    isOpen.value = false;
  }
}

onMounted(() => document.addEventListener("click", handleOutsideClick));
onBeforeUnmount(() => document.removeEventListener("click", handleOutsideClick));
</script>

<style scoped>
.fu-color-popover-wrapper {
  display: inline-block;
  position: relative;
}

.fu-color-popover {
  position: absolute;
  z-index: 9999;
  background: #fff;
  border: 1px solid #e2e2e2;
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  padding: 6px;
  transition: opacity 0.2s ease, transform 0.2s ease;
}

/* Subtle fade animation */
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.15s ease;
}
.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
</style>
