<template>
  <div class="fu-status-dropdown" ref="dropdownRef">
    <div v-if="label" class="fu-status-dropdown__label-text">
      {{ label }}
    </div>

    <!-- ====== TRIGGER SECTION ====== -->
    <div v-if="variant === 'button'">
      <!-- Button trigger -->
      <button class="fu-status-dropdown__button" @click="toggleDropdown">
        <div v-if="multiple">
          <template v-if="selectedItems.length">
            <span v-for="item in selectedItems" :key="item.value" class="fu-tag">
              <component v-if="item.type === 'icon'" :is="item.icon" size="14" />
              <img v-else-if="item.type === 'image'" :src="item.imageUrl" class="fu-option-image" alt="User Image" />
              {{ item.label }}
              <span class="fu-tag__remove" @click.stop="removeItem(item)">Ã—</span>
            </span>
          </template>
          <span v-else class="fu-placeholder">{{ placeholder }}</span>
        </div>

        <div class="flex flex--center flex--gap-md" v-else>
          <component v-if="selectedOption?.type === 'icon'" :is="selectedOption.icon" size="16" />
          <img v-else-if="selectedOption?.type === 'image'" :src="selectedOption.imageUrl" class="fu-option-image"
            alt="User Image" />
          <span>{{ selectedOption?.label || placeholder }}</span>
        </div>

        <svg class="fu-status-dropdown__chevron" viewBox="0 0 20 20" fill="none">
          <path d="M6 8l4 4 4-4" stroke="currentColor" stroke-width="1.5" />
        </svg>
      </button>
    </div>

    <!-- Input variant trigger -->
    <div v-else class="fu-status-dropdown__input-trigger">
      <FusionTextInput :size="size" :readonly="true" :variant="'outline'" :formWrapperWidth="formWrapperWidth"
        :placeholder="selectedOption?.label || placeholder" @focus="toggleDropdown" @click="toggleDropdown" />
    </div>

    <!-- ====== DROPDOWN MENU ====== -->
    <teleport to="body">
      <transition name="fade">
        <div v-if="isOpen" class="fu-status-dropdown__menu" :style="dropdownStyle" ref="menuRef">
          <!-- Search -->
          <div v-if="searchable" class="fu-search-wrapper">
            <FusionTextInput class="fu-search-input" type="text" v-model="searchQuery" :placeholder="searchPlaceholder"
              :size="size" formWrapperWidth="100%">
              <template #right>
                <button class="fu-search-clear" @click="searchQuery = ''">Ã—</button>
              </template>
            </FusionTextInput>
          </div>

          <!-- Options -->
          <div v-if="filteredOptions.length" class="fu-options-scroll scrollbar__control customScrollBar">
            <div v-for="item in filteredOptions" :key="item.value" class="fu-status-dropdown__item"
              @click="selectItem(item)">
              <component v-if="item.type === 'icon'" :is="item.icon" size="16" />
              <img v-else-if="item.type === 'image'" :src="item.imageUrl" class="fu-option-image" alt="" />
              <span>{{ item.label }}</span>
            </div>

            <!-- Action Slot styled like options -->
            <div class="fu-status-dropdown__slot-actions" ref="actionsRef">
              <slot name="actions" />
            </div>
          </div>



          <div v-else class="fu-status-dropdown__empty">
            {{ noResultsText }}
          </div>
        </div>
      </transition>
    </teleport>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, watch, onMounted, onBeforeUnmount, nextTick } from "vue";
import FusionTextInput from "../TextInput/FusionTextInput.vue";

interface Option {
  label: string;
  value: string | number;
  type?: "icon" | "image";
  icon?: any;
  imageUrl?: string;
}

interface Props {
  options: Option[];
  modelValue?: Option | Option[] | null;
  multiple?: boolean;
  placeholder?: string;
  searchable?: boolean;
  noResultsText?: string;
  searchPlaceholder?: string;
  label?: string;
  variant?: "button" | "input";
  size?: "sm" | "md" | "lg";
  formWrapperWidth?: string;
}

const props = withDefaults(defineProps<Props>(), {
  modelValue: null,
  multiple: false,
  placeholder: "Select...",
  searchable: true,
  noResultsText: "No results found",
  searchPlaceholder: "Search...",
  label: "",
  variant: "button",
  size: "md",
  formWrapperWidth: "",
});

const emit = defineEmits(["update:modelValue"]);

const isOpen = ref(false);
const searchQuery = ref("");
const actionsRef = ref<HTMLElement | null>(null);
const dropdownRef = ref<HTMLElement | null>(null);
const menuRef = ref<HTMLElement | null>(null);
const dropdownStyle = ref<Record<string, string>>({});
const selectedOption = ref<Option | null>(null);
const selectedItems = ref<Option[]>([]);

// --- Sync model ---
watch(
  () => props.modelValue,
  (val) => {
    if (props.multiple && Array.isArray(val)) selectedItems.value = val;
    else selectedOption.value = val as Option | null;
  },
  { immediate: true }
);

// --- Toggle dropdown ---
function toggleDropdown() {
  isOpen.value = !isOpen.value;

  if (isOpen.value) {
    nextTick(() => {
      const trigger = dropdownRef.value?.querySelector("button, input") as HTMLElement | null;
      const menu = menuRef.value;
      if (trigger && menu) {
        const rect = trigger.getBoundingClientRect();
        const viewportHeight = window.innerHeight;
        let top = rect.bottom + window.scrollY;
        const bottomSpace = viewportHeight - rect.bottom;
        const openUp = bottomSpace < 200 && rect.top > 200;

        if (openUp) top = rect.top + window.scrollY - menu.offsetHeight - 8;

        dropdownStyle.value = {
          position: "absolute",
          top: `${top}px`,
          left: `${rect.left + window.scrollX}px`,
          width: `${rect.width}px`,
          zIndex: "9999",
        };
      }

      // Auto-focus search box
      if (props.searchable) {
        nextTick(() => {
          const inputEl = menuRef.value?.querySelector("input[type='text']") as HTMLInputElement | null;
          if (inputEl) {
            inputEl.focus();
            inputEl.select();
          }
        });
      }
    });
  }
}

// --- Handle selecting options ---
function selectItem(item: Option) {
  if (props.multiple) {
    const exists = selectedItems.value.find((i) => i.value === item.value);
    selectedItems.value = exists
      ? selectedItems.value.filter((i) => i.value !== item.value)
      : [...selectedItems.value, item];
    emit("update:modelValue", selectedItems.value);
  } else {
    selectedOption.value = item;
    emit("update:modelValue", item);
    isOpen.value = false;
  }
}

function removeItem(item: Option) {
  selectedItems.value = selectedItems.value.filter((i) => i.value !== item.value);
  emit("update:modelValue", selectedItems.value);
}

// --- Filter search ---
const filteredOptions = computed(() => {
  if (!searchQuery.value) return props.options;
  return props.options.filter((opt) =>
    opt.label.toLowerCase().includes(searchQuery.value.toLowerCase())
  );
});

// --- Handle slot action click (modal etc.) ---
const handleActionClick = (e: Event) => {
  e.stopPropagation();
  e.preventDefault();
  isOpen.value = false;

  // let modal open next frame
  requestAnimationFrame(() => {
    const el = e.target as HTMLElement;
    el?.dispatchEvent(new Event("click", { bubbles: true }));
  });
};

// --- Global focus + click handling ---
// Closes dropdown when focus leaves both trigger and dropdown
function handleFocusChange() {
  setTimeout(() => {
    const active = document.activeElement as Node | null;
    if (
      !dropdownRef.value?.contains(active) &&
      !menuRef.value?.contains(active)
    ) {
      isOpen.value = false;
    }
  }, 80);
}

// Closes dropdown when clicking outside
function closeDropdownOnClick(e: MouseEvent) {
  const target = e.target as Node | null;
  if (
    dropdownRef.value?.contains(target) ||
    menuRef.value?.contains(target)
  ) return;
  isOpen.value = false;
}

// Closes dropdown on window resize
function handleResize() {
  if (isOpen.value) isOpen.value = false;
}

// --- Mount/unmount listeners ---
onMounted(() => {
  document.addEventListener("click", closeDropdownOnClick);
  document.addEventListener("focusin", handleFocusChange);
  document.addEventListener("focusout", handleFocusChange);
  window.addEventListener("resize", handleResize);

  if (actionsRef.value) {
    actionsRef.value.addEventListener("click", handleActionClick, true);
  }
});

onBeforeUnmount(() => {
  document.removeEventListener("click", closeDropdownOnClick);
  document.removeEventListener("focusin", handleFocusChange);
  document.removeEventListener("focusout", handleFocusChange);
  window.removeEventListener("resize", handleResize);

  if (actionsRef.value) {
    actionsRef.value.removeEventListener("click", handleActionClick);
  }
});
</script>



