<template>
  <div class="fu-avatar-group" @click="$emit('click')">
    <template v-for="(user, index) in visibleAvatars" :key="user.id || index">
      <FuAvatar :src="user.src" :alt="user.alt" :size="size" />
    </template>

    <div
      v-if="hiddenCount > 0"
      class="fu-avatar fu-avatar--more"
      :class="`fu-avatar--${size}`"
    >
      +{{ hiddenCount }}
    </div>
  </div>
</template>

<script setup lang="ts">
import FuAvatar from "./FuAvatar.vue";

interface User {
  id?: string | number;
  src: string;
  alt?: string;
}

const props = defineProps<{
  users: User[];
  max?: number;
  size?: "xs" | "sm" | "md" | "lg" | "xl" | "2xl" | "3xl";
}>();

defineEmits<{
  (e: "click"): void;
}>();

const max = props.max ?? 3;
const visibleAvatars = props.users.slice(0, max);
const hiddenCount = props.users.length - max;
</script>

