.fu-avatar-group {
  display: flex;
  align-items: center;
  cursor: pointer;

  .fu-avatar {
    margin-left: -0.5rem;

    &:first-child {
      margin-left: 0;
    }
  }

  .fu-avatar--more {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background: #374151;
    color: #fff;
    font-weight: 600;
    border: 2px solid #fff;


    &.fu-avatar--xs {
      width: 20px; height: 20px; font-size: 0.625rem;
    }
    &.fu-avatar--sm {
      width: 28px; height: 28px; font-size: 0.75rem;
    }
    &.fu-avatar--md {
      width: 36px; height: 36px; font-size: 0.75rem;
    }
    &.fu-avatar--lg {
      width: 48px; height: 48px; font-size: 0.875rem;
    }
    &.fu-avatar--xl {
      width: 64px; height: 64px; font-size: 1rem;
    }
    &.fu-avatar--2xl {
      width: 80px; height: 80px; font-size: 1.125rem;
    }
    &.fu-avatar--3xl {
      width: 96px; height: 96px; font-size: 1.25rem;
    }
  }
}
</style>
