<template>
  <div class="fu-input-wrapper" :style="{ width: formWrapperWidth }">
    <label v-if="label" class="fu-input-label">
      {{ label }}
      <span v-if="required" class="fu-input-required">*</span>
    </label>

    <div
      class="fu-input-container"
      :class="[
        `fu-input--${size}`,
        `fu-input--${variant}`,
        { 'fu-input--error': error },
      ]"
    >
      <input
        v-bind="$attrs"
        class="fu-input"
        :type="showPassword ? 'text' : 'password'"
        :placeholder="placeholder"
        :disabled="disabled"
        :required="required"
        v-model="innerValue"
      />

      <!-- Toggle button -->
      <button
        type="button"
        class="fu-password-toggle"
        @click="togglePassword"
      >
        <component
          :is="showPassword ? EyeOff : Eye"
          class="fu-password-icon"
        />
      </button>
    </div>

    <span v-if="error" class="fu-input-error">{{ error }}</span>
  </div>
</template>

<script setup lang="ts">
import { ref, watch } from "vue"
import { Eye, EyeOff } from "lucide-vue-next"

interface Props {
  modelValue?: string
  label?: string
  placeholder?: string
  size?: "sm" | "md" | "lg"
  variant?: "subtle" | "outline" | "plain"
  disabled?: boolean
  error?: string | null
  required?: boolean
  formWrapperWidth?: string
}

const props = withDefaults(defineProps<Props>(), {
  modelValue: "",
  label: "",
  placeholder: "Enter password",
  size: "sm",
  variant: "outline",
  disabled: false,
  error: null,
  required: false,
  formWrapperWidth: "fit-content",
})

const emit = defineEmits<{
  (e: "update:modelValue", value: string): void
}>()

const innerValue = ref(props.modelValue)
const showPassword = ref(false)

const togglePassword = () => {
  showPassword.value = !showPassword.value
}

watch(innerValue, (val) => emit("update:modelValue", val))
watch(() => props.modelValue, (val) => (innerValue.value = val))
</script>

<style lang="scss">
/* import base text input styles (global scope) */
@import "../textInput/FusionTextInput.scss";

/* Allow absolute positioning inside */
.fu-input-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* Toggle Button */
.fu-password-toggle {
  position: absolute;
  right: 0.6rem;
  top: 50%;
  transform: translateY(-50%);
  background: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--fu-color-text-light, #777);
  transition: color 0.2s ease;

  &:hover {
    color: var(--fu-color-text);
  }

  .fu-password-icon {
    width: 18px;
    height: 18px;
    pointer-events: none;
  }
}



</style>
