<template>
  <button
    type="button"
    class="fu-switch"
    :class="[{ 'fu-switch--checked': modelValue, disabled }, sizeClass]"
    role="switch"
    :aria-checked="modelValue"
    :disabled="disabled"
    @click="toggle"
  >
    <span class="fu-switch__thumb"></span>
  </button>
</template>

<script lang="ts" setup>
import { computed } from "vue"

interface Props {
  modelValue: boolean
  disabled?: boolean
  size?: "sm" | "md" | "lg"
}

const props = defineProps<Props>()
const emit = defineEmits<{ (e: "update:modelValue", value: boolean): void }>()

function toggle() {
  if (props.disabled) return
  emit("update:modelValue", !props.modelValue)
}

const sizeClass = computed(() => {
  switch (props.size) {
    case "sm":
      return "fu-switch--sm"
    case "lg":
      return "fu-switch--lg"
    default:
      return "fu-switch--md"
  }
})
</script>

<style scoped>
.fu-switch {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  width: 38px;
  height: 22px;
  border-radius: 9999px;
  background-color: #e5e7eb; /* gray-200 */
  cursor: pointer;
  transition: background-color 0.2s ease, box-shadow 0.2s ease;
  border: none;
  outline: none;
}

.fu-switch__thumb {
  position: absolute;
  top: 2px;
  left: 2px;
  width: 18px;
  height: 18px;
  background-color: white;
  border-radius: 50%;
  transition: transform 0.2s ease;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
}

.fu-switch--checked {
  background-color: #2563eb; /* blue-600 */
}
.fu-switch--checked .fu-switch__thumb {
  transform: translateX(16px);
}

/* Disabled */
.fu-switch.disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Sizes */
.fu-switch--sm {
  width: 30px;
  height: 18px;
}
.fu-switch--sm .fu-switch__thumb {
  width: 14px;
  height: 14px;
  top: 2px;
  left: 2px;
}
.fu-switch--sm.fu-switch--checked .fu-switch__thumb {
  transform: translateX(12px);
}

.fu-switch--lg {
  width: 48px;
  height: 28px;
}
.fu-switch--lg .fu-switch__thumb {
  width: 22px;
  height: 22px;
  top: 3px;
  left: 3px;
}
.fu-switch--lg.fu-switch--checked .fu-switch__thumb {
  transform: translateX(20px);
}
</style>
