<template>
  <div class="fu-theme-selector">

    <div class="fu-theme-grid">
      <div
        v-for="theme in themes"
        :key="theme.key"
        class="fu-theme-card"
        :class="{ 'is-active': modelValue === theme.key }"
        @click="selectTheme(theme.key)"
        tabindex="0"
        role="button"
      >
        <!-- Preview box -->
        <div
          class="fu-theme-preview"
          :class="`fu-theme-preview--${theme.key}`"
        >
          <div class="fu-theme-header"></div>
          <div class="fu-theme-body"></div>
          <div class="fu-theme-footer"></div>

          <div v-if="modelValue === theme.key" class="fu-theme-check">
            <Check class="fu-check-icon" />
          </div>
        </div>

        <span class="fu-theme-label">{{ theme.name }}</span>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { Check } from "lucide-vue-next";
import { defineProps, defineEmits } from "vue";

interface ThemeOption {
  key: "light" | "dark" | "auto" | string;
  name: string;
}

const props = withDefaults(
  defineProps<{
    modelValue?: string;
    themes?: ThemeOption[];
  }>(),
  {
    modelValue: "auto",
    themes: () => [
      { key: "light", name: "Day Light" },
      { key: "dark", name: "Timeless Night" },
      { key: "auto", name: "Automatic" },
    ],
  }
);

const emit = defineEmits(["update:modelValue"]);

function selectTheme(themeKey: string) {
  emit("update:modelValue", themeKey);
}
</script>

