<template>
  <transition name="drawer-fade">
    <div v-if="open" class="fu-drawer">
      <div class="fu-drawer__backdrop" @click="$emit('close')"></div>

      <div
        class="fu-drawer__panel"
        :class="[
          `fu-drawer__panel--${position}`,
          `fu-drawer__panel--${size}`,
          { 'fu-drawer__panel--with-controls': showControls }
        ]"
      >
        <div
          class="fu-drawer__header"
          :class="{ 'border-bottom-0': hideHeaderBorder }"
          v-if="$slots.header || showControls"
        >
          <div class="fu-drawer__header-content">
            <slot name="header"></slot>
          </div>

          <div class="fu-drawer__header-actions" v-if="showControls">
            <FusionActionButton
              size="sm"
              variant="subtle"
              :icon="X"
              @click="$emit('close')"
            />
          </div>
        </div>

        <div class="fu-drawer__body">
          <slot class="slot-body" />
        </div>

        <div
          class="fu-drawer__nav"
          :class="[
            position === 'right' ? 'fu-drawer__nav--left' : '',
            position === 'left' ? 'fu-drawer__nav--right' : '',
            position === 'bottom' ? 'fu-drawer__nav--center' : ''
          ]"
        >
          <FusionActionButton
            v-if="canPrev"
            size="sm"
            variant="subtle"
            :icon="ChevronUp"
            @click="$emit('prev')"
          />
          <FusionActionButton
            v-if="canNext"
            size="sm"
            variant="subtle"
            :icon="ChevronDown"
            @click="$emit('next')"
          />
        </div>
      </div>
    </div>
  </transition>
</template>

<script setup lang="ts">
import { ChevronUp, ChevronDown, X } from "lucide-vue-next";
import { onMounted, onBeforeUnmount } from "vue";
import FusionActionButton from "@/components/actionButton/FusionActionButton.vue";

export interface FusionDrawerProps {
  open: boolean;
  title?: string;
  position?: "left" | "right" | "bottom";
  size?: "half" | "full";
  showControls?: boolean;
  hideHeaderBorder?: boolean;
  canNext?: boolean;
  canPrev?: boolean;
}

const props = withDefaults(defineProps<FusionDrawerProps>(), {
  position: "right",
  size: "half",
  showControls: true,
  hideHeaderBorder: false,
});

const emit = defineEmits<{
  (e: "close"): void;
  (e: "next"): void;
  (e: "prev"): void;
}>();

function handleKeydown(e: KeyboardEvent) {
  if (e.key === "Escape" && props.open) emit("close");
}

onMounted(() => window.addEventListener("keydown", handleKeydown));
onBeforeUnmount(() => window.removeEventListener("keydown", handleKeydown));
</script>

<style scoped lang="scss">
.fu-drawer {
  position: fixed;
  inset: 0;
  z-index: 999;
  display: flex;
}

.fu-drawer__backdrop {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.45);
  backdrop-filter: blur(2px);
}

.fu-drawer__panel {
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: var(--fu-content-body);
  height: 100%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease;
}

.fu-drawer__panel--right {
  right: 0;
  top: 0;
  border-left: 1px solid var(--fu-border);
}

.fu-drawer__panel--left {
  left: 0;
  top: 0;
  border-right: 1px solid var(--fu-border);
}

.fu-drawer__panel--bottom {
  bottom: 0;
  left: 0;
  width: 100%;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  border-top: 1px solid var(--fu-border);
}

.fu-drawer__panel--half {
  width: 50%;
  max-width: 640px;
}

.fu-drawer__panel--full {
  width: 70%;
  max-width: 1200px;
}

@media (max-width: 768px) {
  .fu-drawer__panel--half,
  .fu-drawer__panel--full {
    width: 100%;
    max-width: none;
  }
}

.fu-drawer__header {
  padding: 1rem;
  border-bottom: 1px solid var(--fu-border);
  background-color: var(--fu-content-body);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.fu-drawer__header-content {
  flex: 1;
}

.fu-drawer__header-actions {
  margin-left: 0.75rem;
}

.border-bottom-0 {
  border-bottom: none !important;
}

.fu-drawer__body {
  flex: 1 1 0;
  height: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.fu-drawer__nav {
  position: absolute;
  top: 0.75rem;
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}

.fu-drawer__nav--left {
  left: -2.25rem;
}

.fu-drawer__nav--right {
  right: -2.25rem;
}

.fu-drawer__nav--center {
  left: 50%;
  transform: translateX(-50%);
}

.drawer-fade-enter-active,
.drawer-fade-leave-active {
  transition: all 0.3s ease;
}

.drawer-fade-enter-from,
.drawer-fade-leave-to {
  opacity: 0;
}

.fu-drawer__panel--bottom {
  animation: slideUp 0.3s ease forwards;
}

@keyframes slideUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
</style>
