<template>
  <div
    class="fu-accordion"
    :class="[`fu-accordion--${theme}`]"
  >
    <div
      v-for="item in items"
      :key="item.key"
      class="fu-accordion__item"
    >
      <button
        class="fu-accordion__header"
        :class="{
          'is-open': isOpen(item.key),
          'is-disabled': item.disabled
        }"
        @click="toggle(item.key)"
        :disabled="item.disabled"
      >
        <div class="fu-accordion__header-content">
          <component
            v-if="item.icon"
            :is="item.icon"
            :size="16"
            class="fu-accordion__icon"
          />
          <span>{{ item.title }}</span>
        </div>

        <svg
          class="fu-accordion__chevron"
          :class="{ 'is-open': isOpen(item.key) }"
          xmlns="http://www.w3.org/2000/svg"
          width="16"
          height="16"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M6 9l6 6 6-6"
          />
        </svg>
      </button>

      <div
        class="fu-accordion__body"
        v-show="isOpen(item.key)"
      >
        <slot :name="item.key" />
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref } from "vue";

export interface AccordionItem {
  key: string;
  title: string;
  icon?: any;
  disabled?: boolean;
}

const props = defineProps<{
  items: AccordionItem[];
  defaultOpen?: string[];
  type?: "single" | "multiple";
  theme?: "default" | "plain"; 
}>();

const openKeys = ref<string[]>(props.defaultOpen || []);

function isOpen(key: string) {
  return openKeys.value.includes(key);
}

function toggle(key: string) {
  const currentlyOpen = isOpen(key);

  if (props.type === "single") {
    openKeys.value = currentlyOpen ? [] : [key];
  } else {
    if (currentlyOpen) {
      openKeys.value = openKeys.value.filter((k) => k !== key);
    } else {
      openKeys.value.push(key);
    }
  }
}
</script>

<style lang="scss" scoped>
