    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: var(--fu-color-text);
    transition: all 0.2s ease;
    font-size: 14px;
    font-weight: 600;
    &:hover:not(:disabled),
    &.is-open {
      background: var(--fu-color-surface-gray-2);
      color: var(--fu-color-text);
    }

    &:disabled {
      opacity: 0.6;
      cursor: not-allowed;
    }
  }

  &__header-content {
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }

  &__icon {
    margin-right: 0.25rem;
  }

  &__chevron {
    transition: transform 0.2s ease;
    &.is-open {
      transform: rotate(180deg);
    }
  }

  &__body {
    // padding: 1rem;
    background: var(--fu-color-surface-white);
    border-top: 1px solid var(--fu-border);
    color: var(--fu-color-text);
