    padding-bottom: 2rem;
    padding: 0.2rem;
  }



  &--plain {
    border: none;
    border-radius: 0;
    background: transparent;

    .fu-accordion__item + .fu-accordion__item {
      border-top: none;
    }

    .fu-accordion__header {
      background: transparent;
      border-radius: 0;
      color: var(--fu-color-text);

      &:hover:not(:disabled),
      &.is-open {
        background: transparent !important;
