<template>
  <div class="fu-input-wrapper" :style="{ width: formWrapperWidth }">
    <!-- Label -->
    <label v-if="label" class="fu-input-label">
      {{ label }}
      <span v-if="required" class="fu-input-required">*</span>
    </label>

    <div class="fu-input-container" :class="[`fu-input--${size}`, `fu-input--${variant}`, { 'fu-input--error': error }]">
      <!-- Left Slot (icon/avatar/etc.) -->
      <div v-if="$slots.left" class="fu-input-icon fu-input-icon--left">
        <slot name="left" />
      </div>

      <!-- Input Field -->
      <input
        v-bind="$attrs"
        class="fu-input "
        :type="type"
        :placeholder="placeholder"
        :disabled="disabled"
        :required="required"
        :inputmode="type === 'number' ? 'numeric' : undefined"
        :pattern="type === 'number' ? '[0-9]*' : undefined"
        :min="type === 'number' ? min : undefined"
        :max="type === 'number' ? max : undefined"
        :step="type === 'number' ? step : undefined"
        v-model="innerValue"
      />

      <!-- Right Slot (clear button, icon, etc.) -->
      <div v-if="$slots.right" class="fu-input-icon fu-input-icon--right">
        <slot name="right" />
      </div>
    </div>

    <!-- Error message -->
    <span v-if="error" class="fu-input-error">{{ error }}</span>
  </div>
</template>

<script setup lang="ts">
import { ref, watch } from "vue";

interface Props {
  modelValue?: string | number;
  label?: string;
  placeholder?: string;
  type?: "text" | "email" | "password" | "number";
  size?: "sm" | "md" | "lg";
  variant?: "subtle" | "outline" | "plain";
  disabled?: boolean;
  error?: string | null;
  min?: number;
  max?: number;
  step?: number;
  required?: boolean;
  formWrapperWidth?: string;
}

const props = withDefaults(defineProps<Props>(), {
  modelValue: "",
  label: "",
  placeholder: "",
  type: "text",
  size: "sm",
  variant: "outline",
  disabled: false,
  error: null,
  min: undefined,
  max: undefined,
  step: undefined,
  required: false,
  formWrapperWidth: "fit-content",
});

const emit = defineEmits<{
  (e: "update:modelValue", value: string | number): void;
}>();

const innerValue = ref(props.modelValue);

// sync input â†’ parent
watch(innerValue, (val) => emit("update:modelValue", val));

// sync parent â†’ input
watch(
  () => props.modelValue,
  (val) => (innerValue.value = val)
);
</script>

