    }

    &:hover:not(:disabled) {
      color: var(--fu-color-text);
    }

    &:disabled {
      cursor: not-allowed;
      opacity: 0.5;
    }

    &__icon {
      display: flex;
    }

    &__title {
      white-space: nowrap;
    }
  }
}

.fu-tabs-buttons {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
  scrollbar-width: thin;
  -webkit-overflow-scrolling: touch;
  white-space: nowrap;
