import { createApp } from "vue";
import type { ComponentPublicInstance } from "vue";
import FusionToast from "@/components/notifications/FusionToast.vue";

export type ToastType = "success" | "error" | "info" | "dark";

export interface ToastOptions {
  message: string;
  type?: ToastType;
  duration?: number;
}

/**
 * Programmatic toast composable
 * Usage:
 * const toast = useToast();
 * toast({ message: "Saved!", type: "success" });
 */
export function useToast() {
  return ({ message, type = "info", duration = 3500 }: ToastOptions): void => {
    const mount = document.createElement("div");
    document.body.appendChild(mount);

    const app = createApp(FusionToast, { message, type, duration });
    const vm = app.mount(mount) as ComponentPublicInstance;

    setTimeout(() => {
      app.unmount();
      document.body.removeChild(mount);
    }, duration + 500);
  };
}
