<template>
  <div class="fu-textarea-wrapper">
    <label v-if="label" class="fu-textarea-label">
      {{ label }}
      <span v-if="required" class="fu-textarea-required">*</span>
    </label>

    <textarea
    v-bind="$attrs"
    class="fu-textarea fu-form-control"
    :class="[
        `fu-textarea--${size}`,
        { 'fu-textarea--error': error },
        variant ? `fu-textarea--${variant}` : null
    ]"
    :placeholder="placeholder"
    :disabled="disabled"
    :rows="rows"
    :required="required"
    v-model="innerValue"
    ></textarea>


    <span v-if="error" class="fu-textarea-error">{{ error }}</span>
  </div>
</template>

<script setup lang="ts">
import { ref, watch } from "vue";

interface Props {
  modelValue?: string;
  label?: string;
    variant?: "subtle" | "outline" | "plain";
  placeholder?: string;
  size?: "sm" | "md" | "lg";
  disabled?: boolean;
  error?: string | null;
  rows?: number;
  required?: boolean;
}

const props = withDefaults(defineProps<Props>(), {
  modelValue: "",
  label: "",
  placeholder: "",
  size: "sm",
  disabled: false,
  error: null,
  rows: 3,
  required: false,
  variant: "subtle",
});

const emit = defineEmits<{
  (e: "update:modelValue", value: string): void;
}>();

const innerValue = ref(props.modelValue);

watch(innerValue, (val) => emit("update:modelValue", val));
watch(
  () => props.modelValue,
  (val) => (innerValue.value = val)
);
</script>

<style scoped lang="scss">
@import "./FusionTextArea.scss";


</style>
