<template>
  <div class="fu-task-list">
    <!-- Left Section -->
    <div class="fu-task-left flex flex--gap-xl flex--align-center">
      <!-- Task Info -->
      <div class="flex flex--column flex--gap-lg w-100">
        <div class="fu-task-title">{{ title }}</div>

        <div class="fu-task-meta">
          <div class="fu-task-priority">
            <span class="fu-priority-dot" :style="{ backgroundColor: priorityColor || '#999' }"></span>
            <span class="fu-priority-label">{{ priorityLabel }}</span>
          </div>
          <span class="fu-dot"></span>
          <div class="fu-task-owner">
            <FuAvatar :src="ownerAvatar || undefined" :name="ownerName" :alt="ownerName" size="xs"
              :show-status="false" />
          </div>




        </div>
      </div>
    </div>

    <!-- Right Section: Action dropdown -->
    <div class="fu-task-actions">
      <DropdownMenu :actions="computedActions" :content="false" align="right">
        <template #trigger>
          <FusionActionButton :icon="EllipsisVertical" variant="subtle" size="sm" />
        </template>
      </DropdownMenu>
    </div>
  </div>
</template>

<script setup lang="ts">
import { EllipsisVertical, SquarePen, Trash2 } from "lucide-vue-next";
import FuAvatar from "@/components/avatar/FuAvatar.vue";
import DropdownMenu from "@/components/dropdownMenu/DropdownMenu.vue";
import FusionActionButton from "@/components/actionButton/FusionActionButton.vue";
import { computed } from "vue";

interface Action {
  label: string;
  icon?: any;
  onClick?: () => void;
}

interface Props {
  id: string | number;
  title: string;
  ownerName: string;
  ownerAvatar?: string | null;
  priorityLabel: string;
  priorityColor?: string;
}

const props = withDefaults(defineProps<Props>(), {
  ownerAvatar: null,
  priorityColor: "#ccc",
});

const emit = defineEmits<{
  (e: "edit", id: string | number): void;
  (e: "delete", id: string | number): void;
}>();

// Computed actions for dropdown
const computedActions = computed(() => [
  {
    label: "Edit Task",
    icon: SquarePen,
    onClick: () => emit("edit", props.id),
  },
  {
    label: "Delete Task",
    icon: Trash2,
    onClick: () => emit("delete", props.id),
  },
]);
</script>

