<template>
  <div class="fu-dropdown" ref="dropdown">
    <!-- Trigger -->
    <div class="fu-dropdown__trigger" @click="toggleDropdown">
      <slot name="trigger"></slot>
    </div>

    <!-- Teleported menu -->
    <teleport to="body">
      <transition name="fade">
        <div
          v-if="dropdownOpen"
          class="fu-dropdown__menu"
          :class="[`fu-dropdown__menu--${align}`]"
          :style="menuPosition"
        >
          <!-- Optional custom content -->
          <div v-if="content" class="content">
            <slot name="content"></slot>
          </div>

          <!-- Dropdown actions -->
          <div v-for="action in actions" :key="action.label">
            <a class="fu-dropdown__item" @click="performClick(action)">
              <component
                v-if="action.icon"
                :is="action.icon"
                class="fu-dropdown__icon"
              />
              {{ action.label }}
            </a>
          </div>
        </div>
      </transition>
    </teleport>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, onBeforeUnmount, watch } from "vue";
import type { Component, CSSProperties } from "vue";

interface Action {
  label: string;
  icon?: Component;
  onClick?: () => void;
}

interface Props {
  actions: Action[];
  align?: "left" | "center" | "right";
  content?: boolean;
  isOpen?: boolean;           // external control
  closeOnSelect?: boolean;    // auto-close on item click
}

const emit = defineEmits(["open", "close", "update:isOpen"]);

const props = withDefaults(defineProps<Props>(), {
  align: "right",
  content: false,
  isOpen: undefined,
  closeOnSelect: false,
});

const dropdownOpen = ref(false);
const dropdown = ref<HTMLElement | null>(null);
const menuPosition = ref<CSSProperties>({
  top: "0px",
  left: "0px",
});

// Sync internal state with external prop
watch(
  () => props.isOpen,
  (val) => {
    if (typeof val === "boolean") dropdownOpen.value = val;
  }
);

function positionMenu() {
  if (!dropdownOpen.value || !dropdown.value) return;

  const rect = dropdown.value.getBoundingClientRect();
  const baseTop = rect.bottom + window.scrollY + 6;
  let left = rect.left + window.scrollX;

  if (props.align === "center") {
    left += rect.width / 2;
    menuPosition.value.transform = "translateX(-50%)";
  } else if (props.align === "right") {
    left = rect.right + window.scrollX;
    menuPosition.value.transform = "translateX(-100%)";
  } else {
    menuPosition.value.transform = "";
  }

  menuPosition.value = {
    top: `${baseTop}px`,
    left: `${left}px`,
    position: "absolute",
    zIndex: "2000",
    transform: menuPosition.value.transform,
  };
}

function toggleDropdown(event?: MouseEvent) {
  event?.stopPropagation();
  dropdownOpen.value = !dropdownOpen.value;
  emit(dropdownOpen.value ? "open" : "close");
  emit("update:isOpen", dropdownOpen.value);
  if (dropdownOpen.value) positionMenu();
}

function performClick(action: Action) {
  action.onClick?.();
  if (props.closeOnSelect) {
    dropdownOpen.value = false;
    emit("close");
    emit("update:isOpen", false);
  }
}

function handleClickOutside(event: MouseEvent) {
  if (
    dropdownOpen.value &&
    dropdown.value &&
    !dropdown.value.contains(event.target as Node)
  ) {
    dropdownOpen.value = false;
    emit("close");
    emit("update:isOpen", false);
  }
}

function handleResize() {
  if (dropdownOpen.value) {
    dropdownOpen.value = false;
    emit("close");
    emit("update:isOpen", false);
  }
}

onMounted(() => {
  document.addEventListener("click", handleClickOutside);
  window.addEventListener("resize", handleResize);
});

onBeforeUnmount(() => {
  document.removeEventListener("click", handleClickOutside);
  window.removeEventListener("resize", handleResize);
});
</script>


