  padding: 0.375rem;
  z-index: 2000;
  width: max-content;
  max-width: 190px;

  .content {
    padding: 0.4rem 0.75rem;
    border-bottom: 1px solid var(--fu-border);
    margin-bottom: 0.3rem;
  }

  &--left {
    transform-origin: top left;
  }
  &--center {
    transform-origin: top center;
  }
  &--right {
    transform-origin: top right;
  }
}

.fu-dropdown__item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.4rem 0.75rem;
  color: var(--fu-color-text);
  text-decoration: none;
  border-radius: 4px;
  transition: background-color 0.15s;
  font-weight: 400;

  &:hover {
    background-color: var(--fu-surface-alt);
    cursor: pointer;
  }
}

.fu-dropdown__icon {
  width: 16px;
  height: 16px;
  flex-shrink: 0;
  color: var(--fu-color-text);
}

/* Fade transition */
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.15s ease;
}
.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
</style>
