  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;

  &__wrapper {
    position: relative;
    display: inline-block;
  }

  &__image,
  &__placeholder {
    border-radius: 50%;
    object-fit: cover;
    display: inline-block;
  }

  &__placeholder {
    background: #e5e7eb;
    color: #374151;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    text-transform: uppercase;
  }

  &__name {
    font-weight: inherit;
    color: var(--fu-color-text);
  }


  .fu-status-dot {
    position: absolute;
    bottom: 2px;
    right: 2px;
    width: 7px;
    height: 7px;
    border: 2px solid #fff;
    border-radius: 50%;
  }

  .fu-status-dot--active { background-color: #22c55e; }
  .fu-status-dot--away { background-color: #facc15; }
  .fu-status-dot--busy { background-color: #ef4444; }
  .fu-status-dot--dnd { background-color: #7c3aed; }
  .fu-status-dot--invisible { background-color: #6b7280; }
  .fu-status-dot--offline { background-color: #9ca3af; }

  /* Sizes */
  &--xs .fu-avatar__image,
  &--xs .fu-avatar__placeholder {
    width: 20px; height: 20px; font-size: 0.625rem;
  }

  &--sm .fu-avatar__image,
  &--sm .fu-avatar__placeholder {
    width: 28px; height: 28px; font-size: 0.75rem;
  }

  &--md .fu-avatar__image,
  &--md .fu-avatar__placeholder {
    width: 36px; height: 36px; font-size: 0.875rem;
  }

  &--lg .fu-avatar__image,
  &--lg .fu-avatar__placeholder {
    width: 48px; height: 48px; font-size: 1rem;
  }

  &--xl .fu-avatar__image,
  &--xl .fu-avatar__placeholder {
    width: 64px; height: 64px; font-size: 1.125rem;
  }

  &--2xl .fu-avatar__image,
  &--2xl .fu-avatar__placeholder {
    width: 80px; height: 80px; font-size: 1.25rem;
  }

  &--3xl .fu-avatar__image,
  &--3xl .fu-avatar__placeholder {
    width: 96px; height: 96px; font-size: 1.5rem;
  }
}
</style>
