<template>
    <label class="fu-checkbox" :class="[`fu-checkbox--${size}`, { 'fu-checkbox--disabled': disabled }]" :for="id">
        <input type="checkbox" class="fu-checkbox__input" :id="id" :checked="modelValue" :disabled="disabled"
            @change="$emit('update:modelValue', ($event.target as HTMLInputElement).checked)" />
        <span class="fu-checkbox__box"></span>
        <span v-if="label" class="fu-checkbox__label">{{ label }}</span>
    </label>
</template>

<script setup lang="ts">
const props = withDefaults(defineProps<{
    modelValue?: boolean;
    label?: string;
    id?: string;
    size?: "sm" | "md";
    disabled?: boolean;
}>(), {
    size: "md",
    modelValue: false,
});

defineEmits<{
    (e: "update:modelValue", value: boolean): void;
}>();
</script>

<style lang="scss" scoped>
@import "FuCheckbox.scss";
</style>
