<template>
  <div class="fu-range-control">
    <!-- Label -->
    <label v-if="label" class="fu-range-label">{{ label }}</label>

    <!-- Slider and Value -->
    <div class="fu-range-track">
      <input
        type="range"
        :min="min"
        :max="max"
        :step="step"
        :value="modelValue"
        @input="onInput"
      />
      <span class="fu-range-value">{{ displayValue }}</span>
    </div>
  </div>
</template>

<script setup lang="ts">
import { computed, defineProps, defineEmits } from "vue"

interface Props {
  modelValue: number | string
  min?: number
  max?: number
  step?: number
  label?: string
  unit?: string // e.g. '%', 'px'
}

const props = withDefaults(defineProps<Props>(), {
  modelValue: 0,
  min: 0,
  max: 100,
  step: 1,
  label: "",
  unit: "",
})

const emit = defineEmits<{
  (e: "update:modelValue", value: number): void
  (e: "change", value: number): void
}>()

function onInput(e: Event) {
  const target = e.target as HTMLInputElement
  const value = Number(target.value)
  emit("update:modelValue", value)
  emit("change", value)
}

const displayValue = computed(() =>
  props.unit ? `${props.modelValue}${props.unit}` : String(props.modelValue)
)
</script>

<style scoped>
.fu-range-control {
  display: flex;
  flex-direction: column;
  gap: 6px;
  width: 100%;
}

.fu-range-label {
  font-size: 13px;
  font-weight: 500;
  color: #374151;
}

.fu-range-track {
  display: flex;
  align-items: center;
  gap: 8px;
  width: 100%;
}

.fu-range-track input[type="range"] {
  flex: 1;
  cursor: pointer;
  accent-color: var(--fu-color-primary, #2563eb);
}

.fu-range-value {
  min-width: 50px;
  text-align: right;
  font-size: 12px;
  color: #6b7280;
}
</style>
